
.logo {
  margin: 2em 0 0;
  width: 140px;
  height: auto;
}
.site-header {
  position: relative;
  width: 85%;
  max-width: 960px;
  margin: 0 auto;
}
@media only screen and (min-width: 992px) {
.site-header {
      width: 100%;
}
}
.toggle {
  position: fixed;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  color: white;
  background-color: #2d9bd6;
  border: 0;
}
.toggle:after {
    content: '';
    position: absolute;
    top: 22px;
    left: 6px;
    width: 38px;
    height: 6px;
    background-color: white;
}
@media only screen and (min-width: 992px) {
.toggle {
      display: none;
}
}
@media only screen and (min-width: 992px) {
.navbar {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
}
}
.navbar ul {
  display: none;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media only screen and (min-width: 992px) {
.navbar ul {
      display: block;
      text-align: right;
}
}
.navbar li {
  display: inline-block;
  text-align: left;
  padding: 0 16px;
  margin: 0;
  letter-spacing: .05em;
}
@media only screen and (min-width: 992px) {
.navbar li {
      font-size: 14px;
}
}
.navbar li a {
    display: inline-block;
    text-decoration: none;
    font-weight: bold;
    color: #7C7C7C;
    transition: color .2s;
    padding: 30px 0;
}
.navbar li a:hover {
      color: #2d9bd6;
}
.navbar #bar {
  display: none;
}
@media only screen and (min-width: 992px) {
.navbar #bar {
      display: block;
      width: 20px;
      height: 3px;
      background: #2d9bd6;
      position: absolute;
      bottom: 12px;
      left: 0;
}
}



